import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import web3 from "./web3";
import airdrop from './airdrop';
import LG from './LG-ERC20';


class Home extends Component {

   state = {
     AirdropAmount:"",
     user:"",
     LGbalance:"",
     Airdrop_LGbalance:"",
     balance:"",
     value:"",
     airdrop_adr:"0x6ce2f1206a8b6736543e357049a7e17f0cb29368",

   };

async componentDidMount(){
  //判断页面是否安装了Metamask
  if(typeof window.ethereum !== 'undefined'){
    const ethereum = window.ethereum
    //禁止页面自动刷新，metamask要求写的
    ethereum.autoRefreshOnNetwoorkChange = false
    try{
      //第一次链接Metamask
      const accounts = await ethereum.enable()
      console.log(accounts)
      //初始化Provider
      const provider = window['ethereum']
      console.log(provider)
      //获取网络ID
      console.log(provider.chainId)
      //获取用户帐号
      window.defaultAccount = accounts[0].toLowerCase()
      console.log(window.defaultAccount)
      //切换Metamask帐号
      ethereum.on('accountsChanged', function (accounts){
        console.log("accountsChanged:" + accounts)
      })
      //切换Metamaskm网络ID
      ethereum.on(function (networkVersion){
        console.log(networkVersion)
      })
    }catch(e){
    }
  }else{
    console.log('没有metamask')
  }
//设置变量中默认用户的帐号
  const user = window.defaultAccount;
  this.setState({user});
  //设置变量中空投量数据
  const AirdropAmount = await airdrop.methods.AirdropAmount().call();
  this.setState({AirdropAmount});
  //获取用户帐号中的LG余额
  const LGbalance = await LG.methods.balanceOf(window.defaultAccount).call();
  this.setState({LGbalance});

  const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdrop_adr).call();
  this.setState({Airdrop_LGbalance});
  //获取用户帐号中的HT余额
  const balance = await web3.eth.getBalance(window.defaultAccount);
  this.setState({balance});
}

getAirdrop =() => {
  airdrop.methods.getAirdrop().send({from:window.defaultAccount});
}

onSubmit = async event =>{
  event.preventDefault();//取消表单的默认提交
  airdrop.methods.setAirdropAmount(web3.utils.toWei(this.state.value,'ether')).send({from:window.defaultAccount});
}


  render(){
  return (

<div style={{fontSize:"17px",lineHeight:"100%"}}>
    <hr/>
    <div>
        <p>您的当前帐户:</p>
        <p style={{fontSize:"15px"}}>{this.state.user}</p>
    </div>
        <hr/>
    <div>
        <h3>您的资产余额</h3>
        <p>HT----{web3.utils.fromWei(this.state.balance,'ether')}</p>
        <p>LG----{web3.utils.fromWei(this.state.LGbalance,'ether')}</p>
        <p style={{color:"blue",fontSize:"10px",lineHeight:"10%"}}>
        LG合约地址: 0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c</p>
    </div>
        <hr/>
    <div>
        <h3>LG空投</h3>
        <p>空投池余额:  {web3.utils.fromWei(this.state.Airdrop_LGbalance,'ether')}LG<nobr style={{color:"blue",fontSize:"10px"}}>（不定期添加余额，少于100时请不要领取）</nobr></p>
        <p>每个帐户可领取数量： {this.state.AirdropAmount/10**18}LG &nbsp;&nbsp;
        <button onClick={() => {this.getAirdrop()}}>领取空投</button></p>
    </div>
        <hr/>
    <div>
        <h3>LG生态之红包发发发</h3>
        <p>敬请期待……</p>
    </div>
      <hr/>
      <hr/>
    <div>
          <p>管理员操作部分</p>
      <form onSubmit = {this.onSubmit}>
        <p>修改空投量
        <input value={this.state.value}
        onChange={event=>{this.setState({value:event.target.value})}}/>
        <button>发送</button></p>
      </form>
    </div>
</div>
  );
  }
}

export default Home;
